import { React, useState,useEffect } from "react";
import { Link, useParams } from "react-router-dom";
import config from "../../config"
import axios from "../../util/axios";
import { Redirect } from 'react-router-dom'

export default function EditProject() {
  const { id } = useParams();
  const projectDetailURl = `/project-detail/${id}`;
  const [project, setProject] = useState([]);
  const [editData, setEditData] = useState({});
  const [redirectTo, setRedirectTo] = useState('')
  
  const handleSubmit = async (evt) => {
    evt.preventDefault();

    try {
      let myForm = document.getElementById('editProject');
      let formData = new FormData(myForm);
      
      const result = await axios({
        method: "patch",
        url: config.apiURL + "/projects/" + id +"/update",
        data: formData,
      });

      if(result.status === 200) {
        if (id) {
          setRedirectTo(`/project-detail/${id}`)
        } else {
          setRedirectTo('/projects')
        }
      }
    } catch (error) {
      console.log('something went wrong while editing project')
      console.error(error)
    }
    
  };

  useEffect(() => {
    async function fetchDetail() {
      try {
        const url = config.apiURL + `/projects/${id}`;
        const projectDetail = await axios.get(url);

        if (!projectDetail.data.detail) {
          setRedirectTo('/projects')
        }
        setProject(projectDetail.data.detail);
        setEditData(projectDetail.data.detail);

      } catch (error) {
        console.log("error occured while fetching project detail");
        console.log("Error => ", error.message);
      }
    }
    fetchDetail();
  }, []);

  let handleChange = (e) => {
    let name = e.target.name;
    let value = null;

    if (e.target.files && e.target.files.length) {
      value = e.target.files[0].name;
    } else {
      value = e.target.value;
    }

    const info = {...editData}
    info[name] = value;
    setEditData(info);
  };

  if (redirectTo.length) {
    return <Redirect push to={redirectTo} />
  }
  
  if (!Object.keys(editData).length) {
    return (
      <div className="d-flex justify-content-center">
        <div className="spinner-border" role="status">
          <span className="sr-only">Loading...</span>
        </div>
      </div>
    )
  } 
  return (
    <div className="projects">
      <div className="container-fluid">
        <div className="row">
          <div className="col-12">
            <nav aria-label="breadcrumb">
              <ol className="breadcrumb">
                <li className="breadcrumb-item">
                  <Link to="/">Home</Link>
                </li>
                <li className="breadcrumb-item">
                  <Link to={projectDetailURl}>{project ? project.name : ''}</Link>
                </li>
                <li className="breadcrumb-item active" aria-current="page">Edit</li>
              </ol>
            </nav>
          </div>

          <div className="col-12 clearfix page-info">
            <h2 className="page-title float-left"> Edit Project :: { project ? project.name : '' } </h2>
            <div className="page-actions float-right">
            <Link to={projectDetailURl} className='btn btn-outline-primary rounded'>Back</Link>
            </div>
          </div>

          <div className="col-12">
            <form onSubmit={handleSubmit} id="editProject">
              <div className="row">
                <div className="col-6 form-cover">
                  <p className="section-title">Project info</p>
                  <div className="form-group">
                    <label htmlFor="projecTitle">Title</label>
                    <input
                      type="text"
                      className="form-control"
                      id="projecTitle"
                      name="name"
                      defaultValue={editData.name}
                      onChange={handleChange}
                      required
                    />
                  </div>
                  <div className="form-group">
                    <label htmlFor="description">Description</label>
                    <textarea
                      className="form-control"
                      id="description"
                      rows="3"
                      name="description"
                      defaultValue={editData.description}
                      onChange={handleChange}
                    ></textarea>
                  </div>
                  <div className="form-group">
                    <label htmlFor="projectlogo">Project Logo</label>
                    <div className="custom-file">
                      <input
                        type="file"
                        className="custom-file-input"
                        name="logo"
                        accept="image/*"
                        id="projectlogo"
                        onChange={handleChange}
                      />
                      <label
                        className="custom-file-label"
                        htmlFor="projectlogo"
                      >
                        { editData.logo ? editData.logo : 'choose file' }
                      </label>
                    </div>
                  </div>
                  <button type="submit" className="btn btn-info btn-block rounded">
                    Update
                  </button>
                </div>
                <div className="col-6">
                  <p className="section-title">Project configuration</p>
                  <div className="form-row">
                    <div className="form-group col-4">
                      <label>Schema</label>
                      <select
                        className="form-control"
                        defaultValue={editData.config.schema}
                        name="schema"
                        onChange={handleChange}
                      >
                        <option value="http">HTTP</option>
                        <option value="https">HTTPS</option>
                      </select>
                    </div>
                    <div className="form-group col-4">
                      <label htmlFor="hostname">Host</label>
                      <input
                        type="text"
                        className="form-control"
                        id="hostname"
                        name="hostname"
                        defaultValue={editData.config.hostname}
                        onChange={handleChange}
                        required
                      />
                    </div>
                    <div className="form-group col-4">
                      <label htmlFor="port">Port</label>
                      <input
                        type="number"
                        className="form-control"
                        id="port"
                        name="port"
                        defaultValue={editData.config.port}
                        onChange={handleChange}
                        required
                      />
                    </div>
                    <div className="form-group col-4">
                      <label htmlFor="port">Next API Call After</label>
                      <input
                        type="number"
                        className="form-control"
                        id="port"
                        name="nextAPICallAfter"
                        defaultValue={editData.config.nextAPICallAfter}
                        onChange={handleChange}
                        min={50}
                        max={3000}
                        required
                      />
                      <small className="form-text text-info">In ms</small>
                    </div>
                    <div className="form-group col-4">
                      <label htmlFor="port">
                        Apply Different Content Types
                      </label>
                      <div className="col-12 p-0">
                        <div className="form-check form-check-inline">
                          <input
                            className="form-check-input"
                            type="radio"
                            name="applyDifferentContentType"
                            value="yes"
                            id="applyDifferentContentType_yes"
                            defaultChecked={editData.config.applyDifferentContentType === true}
                            onChange={handleChange}
                          />
                          <label
                            htmlFor="applyDifferentContentType_yes"
                            className="form-check-label"
                          >
                            Yes
                          </label>
                        </div>
                        <div className="form-check form-check-inline">
                          <input
                            className="form-check-input"
                            type="radio"
                            name="applyDifferentContentType"
                            id="applyDifferentContentType_no"
                            value="no"
                            defaultChecked={editData.config.applyDifferentContentType === false}
                            onChange={handleChange}
                          />
                          <label
                            htmlFor="applyDifferentContentType_no"
                            className="form-check-label"
                          >
                            No{" "}
                          </label>
                        </div>
                      </div>
                    </div>
                    <div className="form-group col-4">
                      <label htmlFor="port">Allow Arbitrary Injection</label>
                      <div className="col-12 p-0">
                        <div className="form-check form-check-inline">
                          <input
                            className="form-check-input"
                            type="radio"
                            name="allowInjection"
                            value="yes"
                            id="allowInjection_yes"
                            defaultChecked={editData.config.allowInjection === true}
                            onChange={handleChange}
                          />
                          <label
                            htmlFor="allowInjection_yes"
                            className="form-check-label"
                          >
                            Yes
                          </label>
                        </div>
                        <div className="form-check form-check-inline">
                          <input
                            className="form-check-input"
                            type="radio"
                            name="allowInjection"
                            value="no"
                            defaultChecked={editData.config.allowInjection === false}
                            id="allowInjection_no"
                            onChange={handleChange}
                          />
                          <label
                            htmlFor="allowInjection_no"
                            className="form-check-label"
                          >
                            No
                          </label>
                        </div>
                      </div>
                    </div>
                  </div>
                </div>
              </div>
            </form>
          </div>
        </div>
      </div>
    </div>
  );
}
