import axios from "axios"

axios.interceptors.request.use(
  config => {
    // to avoid overwriting if another interceptor
    // already defined the same object (meta)
    console.log('sending request => ', config.method.toUpperCase(), config.url)

    config.meta = config.meta || {}
    config.meta.requestStartedAt = new Date().getTime();
    return config;
  },
  error => {
    return Promise.reject(error);
  }
)

axios.interceptors.response.use(
  response => {
    console.log(`Execution time for: ${response.config.url} - ${new Date().getTime() - response.config.meta.requestStartedAt} ms`)
    response.responseTime = new Date().getTime() - response.config.meta.beginTimer;
    return response;
  },
  // Handle 4xx & 5xx responses
  error => {
    console.error(`Execution time for: ${error.config.url} - ${new Date().getTime() - error.config.meta.requestStartedAt} ms`)
    return Promise.reject(error)
  }
)

export function payloadToFormData(project) {
  const formdata = new FormData()

  for (let [field, value] of Object.entries(project)) {
    formdata[field] = value
  }

  return formdata;
} 

export default axios