import "jsoneditor/dist/jsoneditor.css";

import jsoneditor from "jsoneditor";
import React, { Component } from "react";

function getTransformedValue(editor) {
  let headers = {};
  let fields = {};

  if (editor.get().headers) {
    headers = editor.get().headers;
  }

  if (editor.get().fields) {
    fields = editor.get().fields;
  }

  return [headers, fields];
}

const defaultEditorValue = {
  headers: {},
  fields: {},
};

export default class JSONEditor extends Component {
  componentDidMount() {
    
    const editorValue = {
      headers: this.props.formField.data.headers,
      fields: this.props.formField.data.fields
    }

    this.options = {
      mode: "code",
      mainMenuBar: false,
      onChange: () => {
        try {
          const [headers, fields] = getTransformedValue(this.jsoneditor);
          const form = { ...this.props.formField };
          form.data.headers = {
            ...form.data.headers,
            ...headers,
          }
          form.data.fields = fields;

          this.props.setFormField(form);
        } catch (error) {
          console.error(
            "error occured on JSON Editor onChange function",
            error
          );
        }
      },
    };

    this.jsoneditor = new jsoneditor(this.container, this.options);
    this.jsoneditor.set(editorValue || defaultEditorValue);
  }

  componentWillUnmount() {
    if (this.jsoneditor) {
      this.jsoneditor.destroy();
    }
  }

  render() {
    return (
      <div
        className="jsonEditorContainer"
        ref={(elem) => (this.container = elem)}
      ></div>
    );
  }
}
