import React from "react";
import { Link } from "react-router-dom";
import config from '../../config'
import RemoveModal from './remove'

const URLList = ({ project, urls, group, reviewRequest }) => {
  return urls.map((info, index) => { 
    return (
      <li key={index} 
        title="click to review request payload" 
        className="list-group-item list-group-item-action url-list" 
        onClick={(e) => {
          if(e.target.nodeName !== 'LI') {
            return
          }
          document.querySelectorAll('.active-url').forEach(node => node.classList.remove('active-url'))
          e.target.classList.add('active-url')
          reviewRequest({ group, url:info.url, method: info.method })
        }}>
          {info.url}
        <span className="badge badge-primary badge-pill">{info.method}</span>
        <div className='float-right'>
          <Link to={`/edit-endpoint/${project._id}/${info._id}/`} className='btn btn-sm btn-rounded btn-success mr-2'>Edit</Link>
          <RemoveModal btnClass='btn btn-danger btn-sm rounded' title={`Delete ${info.url} ?`} id={info._id} url={`${config.apiURL}/urls/${info._id}/remove`}/>
        </div>
      </li>
    )
  })
}

export default function URLs({ project, urls, reviewRequest }) {
  const urlGroup = urls.map((url, index) => {
    return (
      <li key={index} className="list-group-item root">
        <p className="root-item">{url.group}</p>
        <div className="endpoints">
          <ul className="list-group list-group-flush">
            <URLList project={project} urls={url.data} group={url.group} reviewRequest={reviewRequest}/>
          </ul>
        </div>
      </li>
    )
  })

  return (
    <div className="col-3">
      <ul className="list-group list-group-flush url-lists">
        {urlGroup}
      </ul>
    </div>
  );
}
