import { React, useState } from "react";
import { Link } from "react-router-dom";
import config from "../../config";
import axios from "../../util/axios";
import { Redirect } from "react-router-dom";

export default function AddProject() {
  const [project, setProject] = useState({
    name: "",
    description: "",
    schema: "http",
    hostname: "localhost",
    // nextAPICallAfter: 50,
    applyDifferentContentType: "",
    allowInjection: "",
    projectlogo: null,
  });

  const [redirectTo, setRedirectTo] = useState("");

  const handleSubmit = async (evt) => {
    evt.preventDefault();

    try {
      let myForm = document.getElementById("addProject");
      let formData = new FormData(myForm);

      const result = await axios({
        method: "post",
        url: config.apiURL + "/projects/create",
        data: formData,
      });

      if (result.status === 201) {
        if (result.data._id) {
          setRedirectTo(`/project-detail/${result.data._id}`);
        } else {
          setRedirectTo("/projects");
        }
      }
    } catch (error) {
      console.log("something went wrong while creating project");
      console.error(error);
    }
  };

  let handleChange = (e) => {
    let name = e.target.name;
    let value = null;

    if (e.target.files && e.target.files.length) {
      value = e.target.files[0];
    } else {
      value = e.target.value;
    }

    const info = { ...project };
    info[name] = value;
    setProject(info);
  };

  if (redirectTo.length) {
    return <Redirect push to={redirectTo} />;
  }

  return (
    <div className="projects">
      <div className="container-fluid">
        <div className="row">
          <div className="col-12">
            <nav aria-label="breadcrumb">
              <ol className="breadcrumb">
                <li className="breadcrumb-item">
                  <Link to="/">Home</Link>
                </li>
                <li className="breadcrumb-item">
                  <Link to="/projects">Projects</Link>
                </li>
                <li className="breadcrumb-item active" aria-current="page">
                  Add project
                </li>
              </ol>
            </nav>
          </div>

          <div className="col-12 clearfix page-info">
            <h2 className="page-title float-left"> Add Project </h2>
            <div className="page-actions float-right">
              <Link to="/projects" className="btn btn-primary">
                All Projects
              </Link>
            </div>
          </div>

          <div className="col-12">
            <form id="addProject" onSubmit={handleSubmit}>
              <div className="row">
                <div className="col-6 form-cover">
                  <p className="section-title">Project info</p>
                  <div className="form-group">
                    <label htmlFor="projecTitle">Title</label>
                    <input
                      type="text"
                      className="form-control"
                      id="projecTitle"
                      name="name"
                      onChange={handleChange}
                      required
                    />
                  </div>
                  <div className="form-group">
                    <label htmlFor="description">Description</label>
                    <textarea
                      className="form-control"
                      id="description"
                      rows="3"
                      name="description"
                      onChange={handleChange}
                    ></textarea>
                  </div>
                  <div className="form-group">
                    <label htmlFor="projectlogo">Project Logo</label>
                    <div className="custom-file">
                      <input
                        type="file"
                        className="custom-file-input"
                        name="logo"
                        accept="image/*"
                        id="projectlogo"
                        onChange={handleChange}
                      />
                      <label
                        className="custom-file-label"
                        htmlFor="projectlogo"
                      >
                        Choose file
                      </label>
                    </div>
                  </div>
                  <button type="submit" className="btn btn-info btn-block rounded">
                    Save
                  </button>
                </div>
                <div className="col-6">
                  <p className="section-title">Project configuration</p>
                  <div className="form-row">
                    <div className="form-group col-4">
                      <label>Schema</label>
                      <select
                        className="form-control"
                        value={project.schema}
                        name="schema"
                        onChange={handleChange}
                      >
                        <option value="http">HTTP</option>
                        <option value="https">HTTPS</option>
                      </select>
                    </div>
                    <div className="form-group col-4">
                      <label htmlFor="hostname">Host</label>
                      <input
                        type="text"
                        className="form-control"
                        id="hostname"
                        name="hostname"
                        onChange={handleChange}
                        defaultValue={project.hostname}
                        required
                      />
                    </div>
                    <div className="form-group col-4">
                      <label htmlFor="port">Port</label>
                      <input
                        type="number"
                        className="form-control"
                        id="port"
                        name="port"
                        onChange={handleChange}
                        required
                      />
                    </div>
                    {/* <div className="form-group col-4">
                      <label htmlFor="port">Next API Call After</label>
                      <input
                        type="number"
                        className="form-control"
                        id="port"
                        name="nextAPICallAfter"
                        onChange={handleChange}
                        min={50}
                        max={3000}
                        defaultValue={project.nextAPICallAfter}
                        required
                      />
                      <small className="form-text text-info">In ms</small>
                    </div> */}
                    <div className="form-group col-4">
                      <label htmlFor="port">
                        Apply Different Content Types
                      </label>
                      <div className="col-12 p-0">
                        <div className="form-check form-check-inline">
                          <input
                            className="form-check-input"
                            type="radio"
                            name="applyDifferentContentType"
                            value="yes"
                            id="applyDifferentContentType_yes"
                            onChange={handleChange}
                          />
                          <label
                            htmlFor="applyDifferentContentType_yes"
                            className="form-check-label"
                          >
                            Yes
                          </label>
                        </div>
                        <div className="form-check form-check-inline">
                          <input
                            className="form-check-input"
                            type="radio"
                            name="applyDifferentContentType"
                            id="applyDifferentContentType_no"
                            value="no"
                            checked
                            onChange={handleChange}
                          />
                          <label
                            htmlFor="applyDifferentContentType_no"
                            className="form-check-label"
                          >
                            No{" "}
                          </label>
                        </div>
                      </div>
                    </div>
                    <div className="form-group col-4">
                      <label htmlFor="port">Allow Arbitrary Injection</label>
                      <div className="col-12 p-0">
                        <div className="form-check form-check-inline">
                          <input
                            className="form-check-input"
                            type="radio"
                            name="allowInjection"
                            value="yes"
                            id="allowInjection_yes"
                            checked
                            onChange={handleChange}
                          />
                          <label
                            htmlFor="allowInjection_yes"
                            className="form-check-label"
                          >
                            Yes
                          </label>
                        </div>
                        <div className="form-check form-check-inline">
                          <input
                            className="form-check-input"
                            type="radio"
                            name="allowInjection"
                            value="no"
                            id="allowInjection_no"
                            onChange={handleChange}
                          />
                          <label
                            htmlFor="allowInjection_no"
                            className="form-check-label"
                          >
                            No{" "}
                          </label>
                        </div>
                      </div>
                    </div>
                  </div>
                </div>
              </div>
            </form>
          </div>
        </div>
      </div>
    </div>
  );
}
