import React from "react";

export default function PreviewRequestPayload({ requests, showResponse }) {
  const requestHeaders = (request) => {
    let key = 0;
    return Object.entries(request.headers).map(([field, value]) => {
      return (
        <React.Fragment key={key++}>
          <tr>
            <td>{field}</td>
            <td>{value}</td>
          </tr>
        </React.Fragment>
      );
    });
  };

  const requestTag = requests.map((request, index) => {
    const collapseId = "collapse_" + index
    return (
      <div key={index} className="card border-light">
        <div className="card-header bg-transparent clearfix" id="headingOne">
          <p
            className="btn btn-link font-weight-normal float-left pt-1"
            data-toggle="collapse"
            data-target={"#" + collapseId}
            aria-expanded="true"
            aria-controls={collapseId}
          >
            {request.url}
          </p>
          <button className="btn btn-sm btn-info float-right" onClick={(e) => { showResponse(request.headers['X-Request-ID']) }}>Show Response</button>
        </div>

        <div
          id={collapseId}
          className={index === 0 ? "collapse show" : "collapse" }
          aria-labelledby="headingOne"
          data-parent="#accordion"
        >
          <div className="card-body">
            <div className="payload">
              <div className="title">Headers</div>
              <div className="value">
                <table className="table table-sm table-striped">
                  <tbody>{requestHeaders(request)}</tbody>
                </table>
              </div>

              <div className="title">Request Body</div>
              <div className="value">
                <pre className="request-body" name="code">
                  {JSON.stringify(request.data, null, 4)}
                </pre>
              </div>
            </div>
          </div>
        </div>
      </div>
    );
  });

  return (
    <div className="col-4">
      <div className="card border-0 mb-3">
        <div className="card-header border-0">Generated Request</div>
        <div className="card-body p-0">
          <div id="accordion">{requestTag}</div>
        </div>
      </div>
    </div>
  );
}
