import React, { useEffect, useState } from "react";
import { Link } from 'react-router-dom';
import config from "../../config";
import axios from "../../util/axios";
import RemoveModal from './remove'

export default function ProjectHome() {
  const [projectList, setProjectList] = useState([])

  const fetchProjects = async () => {
    try {
      const result = await axios.get(config.apiURL + '/projects')
      if (result.status === 200) {
        setProjectList(result.data)
      }
    } catch (error) {
      console.log('error occured while fetching projects')
      console.error(error)
    }
  }

  useEffect(() => {
    fetchProjects()
  }, [])

  const projects = projectList.map((p, index) => {
    return (
      <div key={index} className="col-lg-3 col-md-12">
        <div className="project-card">
          <div className="card-body">
            { p.logo &&
                <img src={config.fileURL+p.logo} alt={p.name}/>
              }
            <p className="title">{p.name}</p>
            <p className="description">{p.description}</p>
          </div>
          <div className="card-footer actions">
            <RemoveModal title={`Delete ${p.name} ?`} id={p._id} url={`${config.apiURL + '/projects/remove/' + p._id}`}/>
            <a className='btn btn-outline-primary rounded ml-2' href={ "/project-detail/" + p._id } >View</a>
          </div>
        </div>
        
      </div>
    )
  })

  return (
    <div>
      <div className="projects">
        <div className="container-fluid">
          <div className="row">
            <div className="col-12">
              <nav aria-label="breadcrumb">
                <ol className="breadcrumb">
                  <li className="breadcrumb-item">
                    <Link to="/">Home</Link>
                  </li>
                  <li className="breadcrumb-item active" aria-current="page">
                    Projects
                  </li>
                </ol>
              </nav>
            </div>

            <div className="col-12 clearfix page-info">
              <h2 className="page-title float-left">Projects</h2>
              <div className="page-actions float-right">
                <Link to="/add-project" className="btn btn-outline-primary rounded"> Add project </Link>
              </div>
            </div>
            {projects}
          </div>
        </div>
      </div>
    </div>
  );
}
