import React from "react";

export default function Response({ summary }) {
  const success = summary.success ? true : false
  const response = success ?  summary.success : summary.error

  const responseHeaders = (response) => {
    let key = 0;
    return Object.entries(response.headers).map(([field, value]) => {
      return (
        <React.Fragment key={key++}>
          <tr>
            <td>{field}</td>
            <td>{value}</td>
          </tr>
        </React.Fragment>
      );
    });
  };

  return (
    <div className="col-5">
      <div className="card border-0 mb-3 response-summary">
        <div className="card-header border-0">Response Summary</div>
        <div className="card-body">
          <div className="card-group mb-4">
            <div className={success ? "card text-success border-success" : "card text-danger border-danger"}>
              <div className={success ? "card-header bg-transparent border-success" : "card-header bg-transparent border-danger" }>
                Status
              </div>
              <div className="card-body">
                <h5 className="card-title">{success ? 'Success' : 'Failed'}</h5>
              </div>
            </div>
            <div className={success ? "card text-success border-success" : "card text-danger border-danger"}>
              <div className={success ? "card-header bg-transparent border-success" : "card-header bg-transparent border-danger" }>
                Status Code
              </div>
              <div className="card-body">
                <h5 className="card-title">{response.status}</h5>
              </div>
            </div>
            <div className={success ? "card text-success border-success" : "card text-danger border-danger"}>
              <div className={success ? "card-header bg-transparent border-success" : "card-header bg-transparent border-danger" }>
                Status Text
              </div>
              <div className="card-body">
                <h5 className="card-title">{response.statusText}</h5>
              </div>
            </div>
          </div>

          <div className="payload">
            <div className="title">Headers</div>
            <div className="value">
              <table className="table table-sm table-striped">
                <tbody>
                  {responseHeaders(response)}
                </tbody>
              </table>
            </div>

            <div className="title">Response Body</div>
            <div className="value">
              <pre className="request-body" name="code">
                {JSON.stringify(response.data, null, 4)}
              </pre>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
}
