import React, { useEffect, useState } from "react";
import { Link, useParams, Redirect } from "react-router-dom";
import { toast } from "react-toastify";
import config from "../../config";
import axios from "../../util/axios";
import URLs from "./urls";
import PreviewRequestPayload from "./previewPayload";
import ResponsePayload from "./responsePayload";
import '@popperjs/core/dist/umd/popper'
import 'jquery/dist/jquery'
import 'bootstrap/dist/css/bootstrap.css'
import 'bootstrap/dist/js/bootstrap'

function getRequest(requests, group, url, method) {
  const matchURLs = []
  for (let request of requests) {
    if(request.url.endsWith(url) && request.method === method && request.headers['X-Request-Group'] === group) {
      matchURLs.push(request)
    }
  }
  return matchURLs
}

function getReport(reports, requestID) {
  return reports.find(report => report.requestId === requestID)
}

function TestScore(props) {
  if (!props.tested) {
    return <></>;
  }
  return (
    <div className="col-4">
      <div className="card-deck sf-result">
        <div className="card text-success border-success">
          <div className="card-header bg-transparent border-success">
            Success
          </div>
          <div className="card-body">
            <h5 className="card-title">{props.success}</h5>
          </div>
        </div>
        <div className="card text-danger border-danger">
          <div className="card-header bg-transparent border-danger">Failed</div>
          <div className="card-body">
            <h5 className="card-title">{props.failed}</h5>
          </div>
        </div>
      </div>
    </div>
  );
}

export default function ProjectDetail(props) {
  const { id } = useParams();
  const addEndPointURL = "/add-endpoint/" + id;
  const [detail, setDetail] = useState({});
  const [projectURLs, setProjectURLs] = useState([]);
  const [requests, setRequest] = useState([]);
  const [generatedRequests, setgeneratedRequests] = useState([]);
  const [reports, setReports] = useState([]);
  const [summary, setSummary] = useState({});
  const [redirectTo, setRedirectTo] = useState('')
  const [testScore, setTestScore] = useState({
    tested: false,
    success: 0,
    failed: 0,
  });

  const runTest = () => {
    async function startTest() {
      try {
        const url = config.apiURL + `/projects/${id}/run-test`;
        const result = await axios({
          method: "post",
          url: url,
          headers: {
            "Content-Type": "application/json",
          },
          data: {
            detail
          },
        });

        const reports = result.data
        setReports(reports)
        countTestScore(reports)

        toast.success('Test run successfully', {
          position: "top-right",
          autoClose: 3000,
          hideProgressBar: true,
          closeOnClick: true,
          pauseOnHover: true,
        });
      } catch (error) {
        toast.error(error.response ? error.response.data : "error occured while running test", {
          position: "top-right",
          autoClose: 5000,
          hideProgressBar: true,
          closeOnClick: true,
          pauseOnHover: true,
        });
      }
    }
    startTest();
  };

  function countTestScore(reports) {
    let success = 0, failed = 0;
    for (let report of reports) {
      if(report.success === undefined) {
        failed += 1
      } else {
        success += 1
      }
    }
    
    setTestScore({
      tested: (success || failed) ? true : false,
      success,
      failed
    })
  }

  const generatePayload = () => {
    async function generateRequest() {
      try {
        const url = config.apiURL + `/projects/${id}/generate-request`;
        const result = await axios({
          method: "post",
          url: url,
          headers: {
            "Content-Type": "application/json",
          },
          data: {
            urls: projectURLs,
            detail,
          },
        });
        setRequest(result.data)
        document.querySelectorAll('.active-url').forEach(node => node.classList.remove('active-url'))
        toast.success('Request payloads generated successfully', {
          position: "top-right",
          autoClose: 3000,
          hideProgressBar: true,
          closeOnClick: true,
          pauseOnHover: true,
        });
      } catch (error) {
        toast.error(error.response ? error.response.data : "error occured while generating request payload", {  
          position: "top-right",
          autoClose: 3000,
          hideProgressBar: true,
          closeOnClick: true,
          pauseOnHover: true,
        });
      }
    }
    generateRequest();
  };

  const reviewRequest = (url) => {
    if (!requests.length) {
      return toast.error("Please generate request first", {  
        position: "top-right",
        autoClose: 3000,
        hideProgressBar: true,
        closeOnClick: true,
        pauseOnHover: true,
      });
    }
    const dynamicRequests = getRequest(requests, url.group, url.url, url.method)
    setgeneratedRequests(dynamicRequests)
  }

  const showResponse = (requestID) => {
    if (!reports.length) {
      return toast.error("Unable to find report, Please Run Test first", {  
        position: "top-right",
        autoClose: 3000,
        hideProgressBar: true,
        closeOnClick: true,
        pauseOnHover: true,
      });
    }

    const report = getReport(reports, requestID)
    setSummary(report)
  }

  useEffect(() => {
    async function fetchDetail() {
      try {
        const url = config.apiURL + `/projects/${id}`;
        const projectDetail = await axios.get(url);

        if (!projectDetail.data.detail) {
          setRedirectTo('/projects')
        }

        setDetail(projectDetail.data.detail);
        setProjectURLs(projectDetail.data.urls);
        setRequest(projectDetail.data.requests);
        setReports(projectDetail.data.reports);
        countTestScore(projectDetail.data.reports)

      } catch (error) {
        console.log("error occured while fetching project detail");
        console.log("Error => ", error.message);
      }
    }
    fetchDetail();
  }, []);

  if (redirectTo.length) {
    return <Redirect push to={redirectTo} />
  }
  
  return (
    <div>
      <div className="project-detail">
        <div className="container-fluid">
          <div className="row">
            <div className="col-12">
              <nav aria-label="breadcrumb">
                <ol className="breadcrumb">
                  <li className="breadcrumb-item">
                    <Link to="/">Home</Link>
                  </li>
                  <li className="breadcrumb-item">
                    <Link to="/projects">Projects</Link>
                  </li>
                  <li className="breadcrumb-item active" aria-current="page">
                    Project Detail
                  </li>
                </ol>
              </nav>
            </div>

            <div className="col-12 clearfix page-info">
              <div className="float-left">
                <div className="media">
                { detail.logo &&
                  <img src={config.fileURL+detail.logo} alt={detail.name} className="mr-3 project-logo"/>
                }
                  <div className="media-body">
                    <h5 className="mt-0 project-title">{detail.name}</h5>
                    <p className="project-description text-break">
                      {detail.description}
                    </p>
                  </div>
                </div>
              </div>
              <div className="page-actions float-right">
                <button className="btn rounded btn-warning" onClick={runTest} title='Start test'>
                  Run Test
                </button>
                <button className="btn rounded btn-outline-info ml-2" onClick={generatePayload} title='generate and prepare payload for test'>
                  { requests.length ? 'Regenerate' : 'Generate' } Request
                </button>
                <Link
                  to={addEndPointURL}
                  className="btn btn-outline-primary rounded ml-2">
                  Add URL
                </Link>
                <Link to={`/projects/${id}/edit`}
                  className="btn btn-outline-success rounded ml-2">
                  Edit
                </Link>
              </div>
            </div>

            <div
              className={
                testScore.tested ? "col-8 mb-12 mb-4" : "col-md-12 col-12 mb-4"
              }
            >
              <div className="card project-config">
                <div className="card-header bg-transparent">Configuration</div>
                <div className="card-body">
                  <div className="row">
                    <div className="col-4 mb-2">
                      <ul className="list-group list-group-horizontal-md">
                        <li className="list-group-item title">Hostname</li>
                        <li className="list-group-item value">
                          {detail.config ? detail.config.hostname : ""}
                        </li>
                      </ul>
                    </div>
                    <div className="col-4 mb-2">
                      <ul className="list-group list-group-horizontal-md">
                        <li className="list-group-item title">Base URL</li>
                        <li className="list-group-item value">
                          <a
                            target="_blank"
                            href={detail.config ? detail.config.baseURL : ""}
                          >
                            {detail.config ? detail.config.baseURL : ""}
                          </a>
                        </li>
                      </ul>
                    </div>
                    <div className="col-4 mb-2">
                      <ul className="list-group list-group-horizontal-md">
                        <li className="list-group-item title">Schema</li>
                        <li className="list-group-item value">
                          {detail.config ? detail.config.schema : ""}
                        </li>
                      </ul>
                    </div>
                    <div className="col-4 mb-2">
                      <ul className="list-group list-group-horizontal-md">
                        <li className="list-group-item title">Port</li>
                        <li className="list-group-item value">
                          {detail.config ? detail.config.port : ""}
                        </li>
                      </ul>
                    </div>
                    {/* <div className="col-4 mb-2">
                      <ul className="list-group list-group-horizontal-md">
                        <li className="list-group-item title">
                          Next API Call After
                        </li>
                        <li className="list-group-item value">
                          {detail.config
                            ? `${detail.config.nextAPICallAfter} ms`
                            : ""}
                        </li>
                      </ul>
                    </div> */}
                    <div className="col-4 mb-2">
                      <ul className="list-group list-group-horizontal-md">
                        <li className="list-group-item title">
                          Allow Different Content Type
                        </li>
                        <li className="list-group-item value">
                          {detail.config
                            ? detail.config.applyDifferentContentType
                              ? "Yes"
                              : "No"
                            : ""}
                        </li>
                      </ul>
                    </div>
                    <div className="col-4 mb-2">
                      <ul className="list-group list-group-horizontal-md">
                        <li className="list-group-item title">
                          Allow Arbitary Injection
                        </li>
                        <li className="list-group-item value">
                          {detail.config
                            ? detail.config.allowInjection
                              ? "Yes"
                              : "No"
                            : ""}
                        </li>
                      </ul>
                    </div>
                  </div>
                </div>
              </div>
            </div>

            <TestScore {...testScore} />

            <div className="col-12">
              <div className="row api-control-panel">
                {projectURLs.length ? <URLs project={detail} urls={projectURLs} reviewRequest={reviewRequest}/> : ""}
                {generatedRequests.length ? <PreviewRequestPayload requests={generatedRequests} showResponse={showResponse}/> : ""}
                {Object.keys(summary).length ? <ResponsePayload summary={summary}/> : ""}
                {!projectURLs.length ? (
                  <h4 className="ml-3 alert alert-danger">API url not found !</h4>
                ) : (
                  ""
                )}
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
}
