import jquery from "jquery";
import React from "react";
import { useHistory } from "react-router-dom";
import { toast } from "react-toastify";
import axios from "../../util/axios";

export default function RemoveModel({ title, id, url, btnClass = 'btn btn-outline-danger rounded' }) {
  
  const modelId = "removeModel_" + id
  const history = useHistory()

  const handleModal = () => {
    jquery("#" + modelId).modal('show')
    jquery('.modal-backdrop').css('z-index', 0);
  }

  const handleRemove = (url) => {
    async function remove() {
      try {
        const result = await axios({
          method: "delete",
          url: url
        });
        
        const message = result.data || 'Document removed successfully'
        toast.success(message, {
          position: "top-right",
          autoClose: 3000,
          hideProgressBar: true,
          closeOnClick: true,
          pauseOnHover: true,
        });
        
        jquery("#" + modelId).modal('hide')
        jquery('body').removeClass('modal-open');
        jquery('.modal-backdrop').remove();
        history.go(0)

      } catch (error) {
        toast.error("error occured while deleting document", {
          position: "top-right",
          autoClose: 5000,
          hideProgressBar: true,
          closeOnClick: true,
          pauseOnHover: true,
        });
      }
    }
    remove();
  }

  return (
    <React.Fragment>
      <button type="button" className={btnClass} onClick={handleModal}>
        Remove
      </button>
      <div
        className="modal fade"
        id={modelId}
        tabIndex="-1"
        aria-labelledby={modelId}
        aria-hidden="true"
      >
        <div className="modal-dialog">
          <div className="modal-content">
            <div className="modal-header bg-danger">
              <h5
                className="modal-title text-white"
                style={{ fontSize: 14 }}>
                {title}
              </h5>
              <button
                type="button"
                className="close"
                data-dismiss="modal"
                aria-label="Close"
              >
                <span aria-hidden="true">&times;</span>
              </button>
            </div>
            <div className="modal-footer">
              <button
                type="button"
                className="btn btn-secondary rounded"
                data-dismiss="modal"
              >
                Cancel
              </button>
              <button onClick={(e) => { handleRemove(url) }} className="btn btn-outline-danger rounded">
                Confirm
              </button>
            </div>
          </div>
        </div>
      </div>
    </React.Fragment>
  );
}
