import { ToastContainer } from 'react-toastify';
import 'react-toastify/dist/ReactToastify.css';
import AddProject from "./components/project/add";
import AddEndPoint from "./components/project/add-endpoint";
import EditEndPoint from "./components/project/edit-endpoint";
import ProjectHome from "./components/project/home";
import EditProject from "./components/project/edit";
import Login from "./components/user/login";
// import SignUp from "./components/user/signup";
import ProjectDetail from "./components/project/detail";
import Profile from "./components/user/profile";

import { BrowserRouter as Router, Switch, Route, Link, useLocation } from "react-router-dom";

function App() {
  return (
    <Router>
      <div>
        <nav className="navbar navbar-expand-lg">
          <Link className="navbar-brand" to="/">
            Inspect API
          </Link>
          <button
            className="navbar-toggler"
            type="button"
            data-toggle="collapse"
            data-target="#navbarNavAltMarkup"
            aria-controls="navbarNavAltMarkup"
            aria-expanded="false"
            aria-label="Toggle navigation"
          >
            <span className="navbar-toggler-icon"></span>
          </button>
          <div className="collapse navbar-collapse" id="navbarNavAltMarkup">
            <div className="navbar-nav">
              <Link className="nav-link" to="/projects">
                Projects
              </Link>
            </div>
          </div>
          ​
          <div className="profile floot-right">
            <Link to='/profile'>
              <img
                src="https://i.pinimg.com/originals/17/56/8f/17568fcd478e0699067ca7b9a34c702f.png"
                alt="..."
              /> <span className='text-secondary' style={{ fontSize: 14, paddingLeft: 5 }}>Rabin Bhandari</span>
            </Link>
          </div>
        </nav>
        <ToastContainer/>
        <Switch>
          {/* <Route exact path="/signup">
            <SignUp />
          </Route> */}
          
          <Route exact path="/login">
            <Login />
          </Route>

          <Route exact path="/profile">
            <Profile />
          </Route>

          <Route exact path="/add-endpoint/:projectId">
            <AddEndPoint />
          </Route>

          <Route exact path="/edit-endpoint/:projectId/:urlId">
            <EditEndPoint />
          </Route>

          <Route exact path="/add-project">
            <AddProject />
          </Route>

          <Route exact path="/projects">
            <ProjectHome />
          </Route>

          <Route exact path="/project-detail/:id">
            <ProjectDetail />
          </Route>

          <Route exact path="/projects/:id/edit/">
            <EditProject />
          </Route>

          <Route exact path="/">
            <ProjectHome />
          </Route>

          <Route path="*">
            <NoMatch />
          </Route>
        </Switch>
      </div>
    </Router>
  );
}

function NoMatch() {
  let location = useLocation();

  return (
    <div>
      <h3>
        No match for <code>{location.pathname}</code>
      </h3>
    </div>
  );
}

export default App;
